<template>
    <div class="boxing">
      <h4>设备详细信息</h4>
   <el-form ref="form" :model="form" label-width="150px">
  <el-row>
    <el-col :span="12"> 
      <el-form-item label="appID">
        <el-input v-model="form.appid"></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="12"> 
      <el-form-item label="设备ID">
        <el-input v-model="form.deviceid"></el-input>
      </el-form-item>
    </el-col>
  </el-row>
  <el-row>
    <el-col :span="12">
      <el-form-item label="iot平台设备类型">
        <el-input v-model="form.iotDeviceType"></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="12">
      <el-form-item label="iot平台id">
        <el-input v-model="form.iotDeviceid"></el-input>
      </el-form-item>
    </el-col>
  </el-row>
  <el-row>
    <el-col :span="12">
      <el-form-item label="固件版本">
        <el-input ></el-input>
      </el-form-item>
    </el-col> 
    <el-col :span="12">
      <el-form-item label="查询次数">
        <el-input ></el-input>
      </el-form-item>
    </el-col>
  </el-row>
  <el-row>
    <el-col :span="12">
      <el-form-item label="超时时间">
        <el-input ></el-input>
      </el-form-item>
    </el-col> 
    <el-col :span="12">
      <el-form-item label="查询间隔">
        <el-input ></el-input>
      </el-form-item>
    </el-col>
  </el-row> 
  <el-row>
    <el-col :span="12">
      <el-form-item label="退款指令">
        <el-input ></el-input>
      </el-form-item>
    </el-col> 
    <el-col :span="12">     
      <el-form-item label="退款超时">
        <el-input ></el-input>
      </el-form-item>
    </el-col>
  </el-row> 
  <el-row>
    <el-col :span="12">
      <el-form-item label="回写间隔">
        <el-input ></el-input>
      </el-form-item>
     </el-col> 
    <el-col :span="12">     
      <el-form-item label="收银台数据">
        <el-input ></el-input>
      </el-form-item>
    </el-col>
  </el-row> 
  <el-row>
    <el-col :span="24">
      <el-form-item>
        <el-button type="primary" @click="save">保存</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-col>
  </el-row>
</el-form>
</div>
</template>

<script>
import { constants } from 'fs';
  export default {
    data() {
      return {
        form: {
          appid:'',
          deviceid:'',
          groupConfig:'',
          iotDeviceType:'',
          iotDeviceid:'',
          lastConnect:'',
          online:'',
          payConfig:'',
          realPayConfig:'',
          state:'',
          wsy:''
        },
        config:{
          baudRate:'',
          configId:'',
          configName:'',
          dVersion:'',
          deviceType:'',
          fVersion:'',
          numberofQueries:'',
          overtime:'',
          queryInterval:'',
          refundCmd:'',
          refundOvertime:'',
          sendingInterval:'',
          writeBackCode:''
        },
        wsy:{
          deviceid:'',
          wsyDeviceid:'',
          wsySecrtKey:''
        },
        connectTime:{
          year:'',
          month:'',
          day:'',
          hours:'',
          minutes:'',
          seconds:'',
          date:'',
          time:'',
          timezoneOffset:'',
          nanos:''
        }
      }
    },
    created(){
      var that = this;
      that.form.groupConfig = that.config;
      if(this.$route.query.deviceid){
        const deviceid = this.$route.query.deviceid;
        that.$axios.get('/device/'+deviceid,
                            {headers:{'Authorization':sessionStorage.getItem("auth")}
                          }).then(function(res) {
                          console.log(res.data.data)
                          that.form = res.data.data;

                      }).catch(function (error) {
                          console.log(error);
                      });     
      
      }else{
        
      }      
    },
    methods: {
      save(){
          var that=this
          var deviceid=that.form.deviceid;
          var appid=that.form.appid;
          var iotDeviceid=that.form.iotDeviceid;
          var iotDeviceType=that.form.iotDeviceType;
        if(this.$route.query.deviceid){  // 修改设备
          this.$axios({
            method:'put',
            url:'/device',
            headers:{'Authorization':sessionStorage.getItem("auth")},
            data:{
              deviceid:deviceid,
              appid:appid,
              iotDeviceid:iotDeviceid,
              iotDeviceType:iotDeviceType
            }
          }).then(function(res){
            console.log(res);
            if(res.status==200){
              that.$message({
                    message: '修改成功',
                    type: 'success'
                  });
              that.$router.push("/device/list")    
            }
          }).catch(function (error){
            console.log(error)
          })
          
        }else{//添加新设备

          this.$axios.post('/device',{
              deviceid:deviceid,
              appid:appid,
              iotDeviceid:iotDeviceid,
              iotDeviceType:iotDeviceType
            },{headers:{
                'Authorization':sessionStorage.getItem("auth")
            }
          }).then(function(res){
            console.log(res);
            if(res.status==200){
              that.$message({
                    message: '添加成功',
                    type: 'success'
                  });
              that.$router.push("/device/list")    
            }
          }).catch(function (error){
            console.log(error)
          })
        }
        
      },
      
    }
  }
</script>


<style>
 .boxing{
   padding: 0 50px;
   height: 500px;
   /* background: red; */
   margin: 0 auto;
 }
 h4{
   text-align: center;
   line-height: 80px;
 }
</style>



